import logo from './logo.svg';

import './App.css';
import TerminalComponent from './Terminal2';
import './Mystyles.css';
import { useState } from 'react';
import { motion } from 'framer-motion';
import { FaLinkedin, FaGithub } from "react-icons/fa";
function App() {
  const [mouseHover, setMouseHover] = useState(false);
  const foto = require('./p2.png');
  const onMouseEnter = () => {
    setMouseHover(true);
  }
  const onMouseLeave = () => {
    setMouseHover(false);
  }

  return (
    <div className='background'>
      <div className='headerDiv'>
        <img src={foto} className="image" alt="logo"
          style={{ visibility: mouseHover ? 'hidden' : 'visible' }}
          onMouseEnter={onMouseEnter} />
        <div className='card' style={{ display: mouseHover ? 'flex' : 'none' }}
          onMouseLeave={onMouseLeave} >
          <img src={foto} className="image" alt="logo"
          />
          <a href='https://github.com/vtmattedi' target="_blank">
            <FaGithub className='icon'
              color='black' />
          </a>
          <a href='https://www.linkedin.com/in/vitor-mattedi-dev/'>
            <FaLinkedin className='icon'
              color='#0077B5' />
          </a>
          <span className='cardText'>
            Dev Jr | Software Engineer Student
          </span>
        </div>
        <div className='titleDiv'
        >

          <h1 className='title'>
            Console Adventure Web
          </h1>
        </div>
      </div>
      <div>
        <TerminalComponent />
      </div>
    </div >
  );
}

export default App;
