import { FitAddon } from '@xterm/addon-fit'
import { useEffect, useState, useRef } from 'react'
import { useXTerm } from 'react-xtermjs'
import { Logos } from './Game/Assets/Assets'
import { BasicConsole } from './Game/Base/ConsoleHelp'
import { DefaultColors } from './Game/Base/ConsoleHelp'
import { Game, GameStates } from './Game/Game'
import { GameColors } from './Game/Base/GameColors'
import './Mystyles.css'

const TerminalComponent = () => {
    const { instance, ref } = useXTerm()
    const fitAddon = new FitAddon()
    const CH = new BasicConsole();
    const setupOnce = useRef(false)
    const game = useRef(null)

    useEffect(() => {
        // Load the fit addon
        if (!setupOnce.current && instance) {
            game.current = new Game();
            instance.options = {
                convertEol: true,
                fontSize: 18,
                cursorStyle: 'block',
                theme: {
                    background: DefaultColors.BLACK,
                    foreground: DefaultColors.WHITE,
                },
                scrollback: 0,
                screenReaderMode: true,

            }
            // for (let i = 0; i < 256; i++) {
            //     let s = CH.insert_color(DefaultColors.custom_colors(i), "Color: " + i)
            //     instance?.writeln(s)
            // }
            //Change some pallets
            DefaultColors.BLUE = DefaultColors.custom_colors(117)
            DefaultColors.YELLOW = DefaultColors.custom_colors(227)
            GameColors.class_colors[0].color = DefaultColors.custom_colors(215)
            GameColors.class_colors[2].color = DefaultColors.YELLOW
            GameColors.weapon_colors[2].color = DefaultColors.YELLOW
            GameColors.weapon_colors[1].color = DefaultColors.BLUE


            setupOnce.current = true
            instance?.loadAddon(fitAddon)
            instance?.resize(120, 35)

            const handleResize = () => fitAddon.fit()
            instance?.onKey(key => {
                let command = key.name;
                // console.log(key.domEvent.key.toLowerCase());
                if (key.key) {
                    if (key.domEvent.key.toLowerCase() == " ")
                        game.current.handleInput("space");
                    else
                        game.current.handleInput(key.domEvent.key.toLowerCase());
                }
                GameStates.getInstance().currentState?.render();
            })
            fitAddon.fit()
            window.onresize = () => {
                fitAddon.fit()
                GameStates.getInstance().currentState?.rerender();
            }

            //Load the fit addon
            CH.getWidth = () => {
                return instance?.cols
            }
            CH.write = (text) => {
               //console.log("w", text)
                instance?.write(text)
            }
            CH.clear_screen = () => {
                instance?.clear()
            }
            CH.show_cursor(false);
            GameStates.getInstance().currentState = game.current.mainMenu;
            GameStates.getInstance().currentState.rerender();





            // Handle resize event
            window.addEventListener('resize', handleResize)
            return () => {
                window.removeEventListener('resize', handleResize)
            }
        }
    }, [ref, instance])

    return <div style={{
        height: '100%',
        overflowY: 'hidden',
        overflowX: 'hidden',
        boxShadow: '0px 0px 10px 5px #000000',
        height: "80vh",
        width: "80vw",
        display: 'flex',
        justifyItems: 'center',
        flexDirection: 'column',
        margin: 'auto',



    }} >
        <div ref={ref} />
    </div>
}

export default TerminalComponent